function bookSelector(href: string) {
    switch (href) {
        case '../blogData/books/Data Structures, Algorithms, and Functions in Go.pdf':
            return gopdf;
        case '../blogData/books/Data Structures, Algorithms, and Functions in Go.epub':
            return goepub;
        case '../blogData/books/Data Structures, Algorithms, and Functions in JavaScript.pdf':
            return jspdf;
        case '../blogData/books/Data Structures, Algorithms, and Functions in JavaScript.epub':
            return jsepub;
        case '../blogData/books/ecmascript.pdf':
            return es;
        case '../blogData/books/1-div-wonder.pdf':
            return oneDivWonder;
    }
}
