import {Badge, Card, Table} from "react-bootstrap";
import React from "react";
import {StyledSlantedBadgeContainer} from "./Work";
import {GridResponsive} from "./Grid";

function CommonBadges(props: any) {
    return (
        <>
            <MutedBadge text={props.repo.owner.login}/>
            <LicenseBadge license={props.repo.license}/>
            <span onClick={() => props.filterBy('language', props.repo.language)}>
                                    <LanguageBadge language={props.repo.language}/>
                                </span>
            <span onClick={() => props.filterBy('default_branch', props.repo.default_branch)}>
                                    <DefaultBranchBadge branch={props.repo.default_branch}/>
                                </span>
        </>
    );
}

export function GitHubRepoCards(props: any) {
    return (
        <GridResponsive style={{gridTemplateColumns: "repeat(auto-fit, minmax(24%, 1fr))"}}>
            {
                props.repos.map((repo: any, index: number) => {
                    return <Card key={"github-repo-" + index} border="success"
                                 className="text-center">
                        <Card.Header>{repo.name} </Card.Header>
                        <Card.Body>
                            <Card.Text>
                                {repo.description}
                            </Card.Text>
                            <Card.Text>
                                <small className="text-muted">{new Date(repo.created_at).toUTCString()}</small>
                            </Card.Text>
                            <Card.Text>
                                <LinkBadges repo={repo}/>
                                <Watcher text={'watcher'} count={repo.watchers}/>
                                <Fork text={'watcher'} count={repo.watchers}/>
                            </Card.Text>
                            <Card.Text>
                                <CommonBadges {...props} repo={repo}/>
                            </Card.Text>
                        </Card.Body>
                    </Card>;
                })
            }
        </GridResponsive>
    );
}

export function GitHubRepoTable(props: any) {
    return (
        <Table striped bordered hover size="sm">
            <tbody>
            {
                props.repos.map((repo: any, index: number) => {
                    return <tr key={"github-repo-" + index}>
                        <td style={{
                            whiteSpace: "nowrap",
                            overflow: "hidden",
                            textOverflow: "ellipsis",
                            maxWidth: "400px"
                        }}><LinkBadges repo={repo}/></td>
                        <td style={{
                            whiteSpace: "nowrap",
                            overflow: "hidden",
                            textOverflow: "ellipsis",
                            maxWidth: "400px"
                        }}>{repo.name}
                            <NonZeroMutedBadge text={'issue'} count={repo.issues}/>
                            {/*<NonZeroMutedBadge text={'fork'} count={repo.forks}/>*/}
                            <Fork text={'fork'} count={repo.forks}/>
                            {/*<NonZeroMutedBadge text={'watcher'} count={repo.watchers}/>*/}
                            <Watcher text={'watcher'} count={repo.watchers}/>
                        </td>

                        <td style={{
                            whiteSpace: "nowrap",
                            overflow: "hidden",
                            textOverflow: "ellipsis",
                            maxWidth: "400px"
                        }}>
                            {repo.description}
                        </td>
                        <td style={{
                            whiteSpace: "nowrap",
                            overflow: "hidden",
                            textOverflow: "ellipsis",
                            maxWidth: "400px"
                        }}>
                            <small className="text-muted">{new Date(repo.created_at).toUTCString()}</small>
                        </td>
                        <td style={{
                            whiteSpace: "nowrap",
                            overflow: "hidden",
                            textOverflow: "ellipsis",
                            maxWidth: "400px"
                        }}>
                            <StyledSlantedBadgeContainer>
                                <CommonBadges {...props} repo={repo}/>
                            </StyledSlantedBadgeContainer>
                        </td>
                    </tr>;
                })
            }
            </tbody>
        </Table>
    );
}

export function MutedBadge(props: any) {
    return (
        <Badge variant="light"><small className="text-muted">{props.text}</small></Badge>
    );
}

export function LicenseBadge(props: any) {
    const isNull = props.license === null;
    const isGPL = !isNull && props.license.spdx_id.includes('GPL');
    const isMIT = !isNull && props.license.spdx_id.startsWith('MIT');
    const variant = isNull || isGPL ? "danger" :
        isMIT ? "success" : 'dark';
    const value = isNull ? '' : props.license.spdx_id;

    return (
        <Badge variant={variant}>{value}</Badge>
    );
}

export function LinkBadges(props: any) {
    return (
        <>
            <Badge variant="light"><a href={props.repo.html_url}>GH</a></Badge>
            <Badge variant="light"><a href={props.repo.html_url + "/issues"}>issues</a></Badge>
        </>
    );
}

export function LanguageBadge(props: any) {
    return (
        <Badge variant="warning">{props.language}</Badge>
    );
}

export function NonZeroMutedBadge(props: any) {
    const text = props.count > 1 ? props.text + "s" : props.text;
    const rendered = props.count > 0 ? (<MutedBadge text={props.count + " " + text}/>) : (<></>);
    return (
        <>
            {rendered}
        </>
    );
}

