import React from "react";

import {SkillType} from "../data/skills.data";

import babelSvg from 'svg-icon/dist/svg/logos/babel.svg';
import backboneSvg from 'svg-icon/dist/svg/logos/backbone.svg';
import bashSvg from 'svg-icon/dist/svg/logos/bash.svg';
import bitbucketSvg from 'svg-icon/dist/svg/logos/bitbucket.svg';
import bootstrapSvg from 'svg-icon/dist/svg/logos/bootstrap.svg';
import bowerSvg from 'svg-icon/dist/svg/logos/bower.svg';
import cSvg from 'svg-icon/dist/svg/logos/c.svg';
import chaiSvg from 'svg-icon/dist/svg/logos/chai.svg';
import css3Svg from 'svg-icon/dist/svg/dev/css3.svg';
import debianSvg from 'svg-icon/dist/svg/logos/debian.svg';
import discordSvg from 'svg-icon/dist/svg/logos/discord.svg';
import dockerSvg from 'svg-icon/dist/svg/logos/docker.svg';
import dotnetSvg from 'svg-icon/dist/svg/logos/dotnet.svg';
import expressSvg from 'svg-icon/dist/svg/logos/express.svg';
import gitSvg from 'svg-icon/dist/svg/logos/git.svg';
import githubSvg from 'svg-icon/dist/svg/logos/github.svg';
import gitlabSvg from 'svg-icon/dist/svg/logos/gitlab.svg';
import html5Svg from 'svg-icon/dist/svg/dev/html5.svg';
import jasmineSvg from 'svg-icon/dist/svg/logos/jasmine.svg';
import javascriptSvg from 'svg-icon/dist/svg/logos/javascript.svg';
import jquerySvg from 'svg-icon/dist/svg/logos/jquery.svg';
import typescriptSvg from '../data/typescript.svg';
import golangSvg from '../data/golang.svg';
import ubuntuSvg from 'svg-icon/dist/svg/logos/ubuntu.svg';
import macosxSvg from 'svg-icon/dist/svg/logos/macosx.svg';
import windowsSvg from 'svg-icon/dist/svg/logos/microsoft-windows.svg';
import kaliSvg from '../data/kali.svg';
import webstormSvg from 'svg-icon/dist/svg/logos/webstorm.svg';
import jestSvg from 'svg-icon/dist/svg/logos/jest.svg';
import npmSvg from 'svg-icon/dist/svg/logos/npm.svg';
import vimSvg from 'svg-icon/dist/svg/logos/vim.svg';
import postgresqlSvg from 'svg-icon/dist/svg/logos/postgresql.svg';
import pythonSvg from 'svg-icon/dist/svg/logos/python.svg';


const SVGMap: any = {
    babelSvg,
    backboneSvg,
    bashSvg,
    bitbucketSvg,
    bootstrapSvg,
    bowerSvg,
    cSvg,
    chaiSvg,
    css3Svg,
    debianSvg,
    discordSvg,
    dockerSvg,
    dotnetSvg,
    expressSvg,
    gitSvg,
    githubSvg,
    gitlabSvg,
    html5Svg,
    jasmineSvg,
    javascriptSvg,
    jquerySvg,
    typescriptSvg,
    ubuntuSvg,
    macosxSvg,
    windowsSvg,
    kaliSvg,
    webstormSvg,
    jestSvg,
    npmSvg,
    vimSvg,
    postgresqlSvg,
    pythonSvg,
    golangSvg,
};

function Logo(props: any) {
    return (
        <img
            height={"80px"}
            width={"80px"}
            src={getSVG(props.iconPath)}
            alt={props.iconPath}
            title={props.name}
        />);
}

function SkillLogo(props: any) {
    const skill: SkillType = props.skill;
    return (
        <>
            {
                skill.iconPath !== '' ? (<Logo {...skill} />) : ''
            }
        </>
    );
}

function getSVG(iconPath: string): any {
    let name: string = iconPath.split('.')[0].split('/').pop() + 'Svg';
    // console.log(name);
    if (name.includes("-")) {
        name = name.split('-').pop() || '';
    }
    return SVGMap[name];
}

export {
    getSVG,
    SkillLogo,
    Logo
};
