export default Work;

const StyledWorkCardsContainer = styled.div`
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    @media screen and (max-width: 1300px) {
        grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 900px) {
        grid-template-columns: repeat(1, 1fr);
    }
`;

const StyledWorkCards = styled.div`

    display: inline-block;
    margin: 1rem;

    div.card {
        min-height: 600px;
    }

    @media screen and (max-width: 800px) {
        width: 100%;
        min-width: 500px;
    }
`;

export const MutedSpan = styled.span`
    margin: auto 2rem;
    color: var(--muted-text-color);
    font-size: .7rem;
`;

export function WorkSimple(props: any) {
    return (
        <div>
            {
                props.work.map((w: any, index: number) => {
                    return <div key={"w-" + index} style={{margin: "1rem"}}>
                        <div>
                            <div>
                                <h3>{w.position}</h3>
                                <div>
                                    {`${w.started.year}/${w.started.month} - ${w.stopped.year}/${w.stopped.month}`}
                                </div>
                            </div>
                            <div>
                                <div>
                                    <WorkExperience company={w.company} style={{textAlign: "left"}}/>
                                </div>
                                <div>
                                    {
                                        w.projects.map((v: any, i: number) => {
                                            return <div key={`project-${i}`}>
                                                <p>
                                                    <span style={{fontWeight: "bold"}}>{v.name}</span> - {v.description}
                                                </p>
                                            </div>;
                                        })
                                    }
                                </div>

                                <div>
                                    <div>
                                        <label style={{
                                            margin: "0 .2rem",
                                            fontStyle: "italic"
                                        }}> languages: </label>
                                        {
                                            w.skills.lang.join(', ')
                                        }
                                        <label style={{
                                            margin: "0 .2rem",
                                            fontStyle: "italic"
                                        }}> frameworks: </label>
                                        {
                                            w.skills.framework.join(', ')
                                        }
                                        <label style={{
                                            margin: "0 .2rem",
                                            fontStyle: "italic"
                                        }}>libraries: </label>
                                        {
                                            w.skills.library.join(', ')
                                        }
                                        <label style={{
                                            margin: "0 .2rem",
                                            fontStyle: "italic"
                                        }}>tools: </label>
                                        {
                                            w.skills.tool.join(', ')
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>;
                })
            }
        </div>
    );
}

export function WorkCards(props: any) {
    return (
        <StyledWorkCardsContainer>
            {
                props.work.map((w: any, index: number) => {
                    return <StyledWorkCards key={"w-" + index}>
                        <Card border="success">
                            <Card.Header>
                                <Badge variant="danger">{w.position}</Badge>
                                <MutedSpan>
                                    {`${w.started.year}/${w.started.month} - ${w.stopped.year}/${w.stopped.month}`}
                                </MutedSpan>
                            </Card.Header>
                            <Card.Body>
                                <Card.Text>
                                    <WorkExperience company={w.company}/>
                                </Card.Text>
                                <Card.Text>
                                    <WorkProjects projects={w.projects}/>
                                </Card.Text>

                                <Card.Text>
                                    <StyledSlantedBadgeContainer>
                                        <LanguageBadges languages={w.skills.lang}/><br/>
                                        <FrameworkBadges frameworks={w.skills.framework}/>
                                        <LibraryBadges libraries={w.skills.library}/>
                                        <ToolBadges tools={w.skills.tool}/><br/>
                                    </StyledSlantedBadgeContainer>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </StyledWorkCards>;
                })
            }
        </StyledWorkCardsContainer>
    );
}

export function WorkTable(props: any) {
    return (
        <Table striped hover size={"lg"}>
            <tbody>
            {
                props.work.map((w: any, index: number) => {
                    return <tr key={"w-" + index} style={{minWidth: "600px"}}>
                        <td>
                            <Badge variant="danger">{w.position}</Badge>
                            <hr/>
                            <small className="text-muted">
                                {`${w.started.year}/${w.started.month} - ${w.stopped.year}/${w.stopped.month}`}
                            </small>
                        </td>
                        <td>
                            <WorkExperience company={w.company}/>
                            <WorkProjects projects={w.projects}/>
                        </td>

                        <td style={{width: "25%"}}>
                            <StyledSlantedBadgeContainer>
                                <LanguageBadges languages={w.skills.lang}/><br/>
                                <FrameworkBadges frameworks={w.skills.framework}/>
                                <LibraryBadges libraries={w.skills.library}/><br/>
                                <ToolBadges tools={w.skills.tool}/><br/>
                            </StyledSlantedBadgeContainer>
                        </td>
                    </tr>;
                })
            }
            </tbody>
        </Table>
    );
}

export const StyledSlantedBadgeContainer = styled.div`
    span.badge {
        margin: .2em;
        padding: .4em;
        transform: skew(-9deg);
        font-size: 1rem;
    }
`;

export function LanguageBadges(props: any) {
    return <GenericBadges keyPrefix={"language"} variant={"warning"} data={props.languages}/>;
}

export function ToolBadges(props: any) {
    return <GenericBadges keyPrefix={"tool"} variant={"light"} data={props.tools}/>;
}

export function FrameworkBadges(props: any) {
    return <GenericBadges keyPrefix={"framework"} variant={"success"} data={props.frameworks}/>;
}

export function LibraryBadges(props: any) {
    return <GenericBadges keyPrefix={"library"} variant={"dark"} data={props.libraries}/>;
}

export function GenericBadges(props: any) {
    return (
        <>
            {props.data.map((value: string, index: number) => {
                return (<Badge style={{margin: ".2em", padding: ".4em"}}
                               key={props.keyPrefix + "_" + index}
                               variant={props.variant}>
                    {value}
                </Badge>);
            })}

        </>
    );
}

export function WorkExperience(props: any) {
    return (
        <>
            {props.company.isSame ?
                (<>
                        <h3>{props.company.payroll.name}</h3>
                        <h6>{props.company.payroll.location}</h6>
                    </>
                ) :
                (
                    <>
                        <h3>{props.company.client.name} <Badge>/ {props.company.payroll.name}</Badge></h3>
                        <h6>{props.company.client.location}</h6>
                    </>
                )}
        </>
    );
}

const StyledProjectName = styled.td`
    text-align: right;
    min-width: 25%;
`;

const StyledProjectDescription = styled.td`
    text-align: left;
`;

export function WorkProjects(props: any) {
    return (
        <Table striped size="sm">
            {props.projects.map((project: any, index: number) => {
                return (
                    <tr key={"project-" + index}>
                        <StyledProjectName><Badge variant="primary">{project.name}</Badge></StyledProjectName>
                        <StyledProjectDescription>{project.description}</StyledProjectDescription>
                    </tr>
                );
            })}
        </Table>
    );
}
