import React from 'react';
import {SKILLS, SkillType} from "../data/skills.data";
import {Badge} from "react-bootstrap";
import styled from "styled-components";
import {StyledSlantedBadgeContainer} from "./Work";
import {SkillLogo} from "./Logo";

const SkillsGrid = styled.div`
    display: grid;
    grid-gap: var(--default-min-length);
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));

    .skill-card {
        padding: .51rem 1rem;
        margin: .51rem;
        text-align: center;
        border: thin solid var(--muted-text-color);
        border-radius: calc(2 * var(--default-min-length));
        position: relative;
    }
`;

function Skills() {
    const out = SKILLS
        .sort((a, b) => a.progression.length - b.progression.length)
        .sort((a, b) => b.relevantExperience - a.relevantExperience)
        .map((skill: SkillType, index: number) => {
            return (<div key={'skills-card-' + index}>
                <div
                    className="skill-card"
                    title={`${skill.relevantExperience} years of relevant experience`}
                >
                    <SkillLogo skill={skill}/>
                    <ColoredBadge skill={skill} index={index}/>
                </div>
            </div>);
        });
    return (
        <div style={{margin: "10vh 0"}}>
            <SkillsGrid>
                {out}
            </SkillsGrid>
        </div>
    );
}

export default Skills;

function variantCalculator(type: string) {
    switch (type) {
        case 'pl':
            return 'warning';
        case 'fw':
            return 'danger';
        case 'os':
            return 'dark';
        case 'lib':
            return 'success';
        case 'tool':
            return 'light';
        default:
            return 'light';
    }
}

function ColoredBadge(props: any) {
    const skill: SkillType = props.skill;
    const index: number = props.index;
    const variant = variantCalculator(skill.type);
    const exp = skill.progression[skill.progression.length - 1].year - skill.progression[0].year + 1;
    return (
        <StyledSlantedBadgeContainer>
            <Badge key={'skill-badge-' + index}
                   variant={variant}
                   style={{margin: ".51rem 0 0", padding: "0 1.5rem", fontSize: "1rem"}}
                   title={`${skill.relevantExperience} years of relevant experience with ${exp} years of togetherness`}>

                {skill.name}

                {skill.relevantExperience > 0 &&
                <Badge variant={"primary"}
                       title={`${skill.relevantExperience} years of relevant experience`}
                       style={{
                           padding: "1.2rem .6rem",
                           position: "relative",
                           right: "-1rem",
                           fontWeight: "bolder",
                           fontSize: "1.2rem",
                           transform: "skew(-11deg)",
                           borderRadius: "0",
                           margin: "0 .3rem",
                           textShadow: "1px 1px 2px black",
                       }}>
                    {skill.relevantExperience}
                </Badge>
                }
                {
                    skill.relevantExperience !== exp &&
                    <Badge variant={"light"}
                           style={{
                               padding: ".2rem",
                               position: "relative",
                               right: "-1rem",
                               fontWeight: "bold",
                               margin: "0 .3rem",
                               borderRadius: "0",
                           }}>
                        {exp}
                    </Badge>
                }
            </Badge>
        </StyledSlantedBadgeContainer>
    );
}

function PlainSkillBadge(props: any) {
    const skill: SkillType = props.skill;
    const index: number = props.index;
    return (
        <div>
            <Badge key={'skill-badge-' + index}
                   style={{
                       fontSize: "1.42rem",
                       textShadow: "1px 1px 1px var(--black)",
                       transform: "skew(-11deg)"
                   }}>
                {skill.name}
            </Badge>
        </div>
    );
}
