import React from "react";
import {EDUCATION} from "../data/education.data";
import {Badge, Table} from "react-bootstrap";
import {MutedSpan, StyledSlantedBadgeContainer} from "./Work";

function Education() {
    return (
        <Table>
            {
                EDUCATION.map((edu: any, index: number) => {
                    return (
                        <tr key={"education-" + index}>
                            <td>
                                {edu.institute}
                                <br/>
                                <MutedSpan>
                                    {`${edu.started.year}/${edu.started.month} - ${edu.stopped.year}/${edu.stopped.month}`}
                                </MutedSpan>
                            </td>
                            <td><p>{edu.certificate}</p></td>
                            <td>
                                <StyledSlantedBadgeContainer>
                                    {edu.subjects.map((sub: string, i: number) => {
                                        return (
                                            <Badge style={{margin: ".1rem", padding: "1rem .5rem"}}
                                                   variant={"light"}>{sub}</Badge>
                                        );
                                    })}
                                </StyledSlantedBadgeContainer>
                            </td>
                        </tr>
                    );
                })
            }
        </Table>
    );
}

export default Education;
